import React, { useEffect, useState } from 'react';
import { List, Avatar, Card, Typography, Badge } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import axios from 'axios';
import './styles/OnlineUsers.css'

const { Title } = Typography;

const OnlineUsers = () => {
  const [onlineUsers, setOnlineUsers] = useState([]);

  useEffect(() => {
    const fetchOnlineUsers = async () => {
      try {
        const response = await axios.get('https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records', {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        });

        if (response.data && response.data.list) {
          setOnlineUsers(response.data.list);
        }
      } catch (error) {
        console.error('Erro ao buscar usurios online:', error);
      }
    };

    fetchOnlineUsers();

    const intervalId = setInterval(fetchOnlineUsers, 30000);

    return () => clearInterval(intervalId);
  }, []);

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <div style={{ padding: '20px', maxWidth: '900px', margin: '0 auto' }}>
      <Card bordered={false} style={{ boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)' }}>
        <Title level={3} style={{ textAlign: 'center', marginBottom: '20px' }}>Usurios Online</Title>
        <List
          itemLayout="horizontal"
          dataSource={onlineUsers}
          renderItem={user => (
            <List.Item>
              <List.Item.Meta
                avatar={
                  <Badge
                    color={user.isOnline ? 'green' : 'red'}
                    dot
                    offset={[-10, 5]}
                  >
                    <Avatar src={user.profilePicUrl} icon={<UserOutlined />} />
                  </Badge>
                }
                title={user.name}
                description={
                  user.isOnline
                    ? 'Online agora'
                    : `ltima atividade: ${formatDate(user.lastActiveAt)}`
                }
              />
            </List.Item>
          )}
        />
      </Card>
    </div>
  );
};

export default OnlineUsers;
