import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Form, Input, Button, Card, Typography, message } from 'antd';
import axios from 'axios';
import './styles/Login.css';
import { getBackgroundConfig } from '../firebaseUtils'; // Importando as funes necessrias

const { Title } = Typography;

const Login = ({ onLogin, onLogout}) => {
  const [background, setBackground] = useState('');
  const [loading, setLoading] = useState(false);
  const [userProfile, setUserProfile] = useState(null); // Novo estado para armazenar o perfil do usurio
  const navigate = useNavigate();

  useEffect(() => {
    const fetchBackground = async () => {
      try {
        const fetchedBackgroundUrl = await getBackgroundConfig();
        if (fetchedBackgroundUrl) {
          setBackground(fetchedBackgroundUrl);
        }
      } catch (error) {
        console.error('Erro ao carregar o fundo:', error);
      }
    };

    fetchBackground();
  }, []);


  const setUserOnline = async (user) => {
    try {
      if (!user.id) {
        throw new Error('ID do usurio no definido');
      }
  
      const response = await axios.patch(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records/`,
        {
          Id: user.id,
          name: user.name,
          email: user.email,
          password: user.password,
          Cargo1: user.Cargo1,
          username: user.username,
          profilePicUrl: user.profilePic,
          isOnline: true,
          lastActiveAt: new Date().toISOString(),
        },
        {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        }
      );
  
      console.log('Usurio atualizado:', response.data);
    } catch (error) {
      console.error('Erro ao definir usurio como online:', error.response?.data || error.message);
    }
  };

  const handleLogin = async (values) => {
    setLoading(true);
    try {
      const { data } = await axios.get(
        'https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records',
        {
          params: {
            where: `(username,eq,${values.username})`,
          },
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        }
      );
  
      if (data.list.length > 0) {
        const user = data.list[0];
        if (user.password === values.password) {
          const userProfile = {
            id: user.Id, // Certifique-se de que "Id" est corretamente preenchido
            name: user.name || user.username,
            email: user.email,
            profilePic: user.profilePicUrl,
            Cargo1: user.Cargo1,
            username: user.username,
            password: user.password, // Adiciona a senha ao userProfile para o update
            isOnline: true,
          };
  
          // Atualiza o status do usurio para online
          await setUserOnline(userProfile);
  
          onLogin(userProfile);
          navigate('/home');
        } else {
          message.error('Senha incorreta.');
        }
      } else {
        message.error('Usurio no encontrado.');
      }
    } catch (error) {
      console.error('Erro na autenticao:', error);
      message.error('Erro na autenticao.');
    } finally {
      setLoading(false);
    }
  };
    
  
  
  const setUserOffline = async (user) => {
    try {
      console.log("Iniciando a funo setUserOffline...");
  
      if (!user.id) {
        console.error('ID do usurio no definido');
        throw new Error('ID do usurio no definido');
      }
  
      console.log(`ID do usurio: ${user.id}`);
      console.log('Dados do usurio antes do update:', {
        Id: user.id,
        name: user.name,
        email: user.email,
        password: user.password, // Certifique-se de que a senha esteja disponvel
        Cargo1: user.Cargo1,
        username: user.username,
        profilePicUrl: user.profilePic,
        isOnline: false,
        lastActiveAt: new Date().toISOString(),
      });
  
      const response = await axios.patch(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/m0wcogamwt1qc5e/records/`,
        {
          Id: user.id,
          name: user.name,
          email: user.email,
          password: user.password, // Certifique-se de que a senha esteja disponvel
          Cargo1: user.Cargo1,
          username: user.username,
          profilePicUrl: user.profilePic,
          isOnline: false,
          lastActiveAt: new Date().toISOString(),
        },
        {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        }
      );
  
      console.log('Resposta do servidor aps o update:', response.data);
    } catch (error) {
      console.error('Erro ao definir usurio como offline:', error.response?.data || error.message);
    }
  };
  
  const handleLogout = async () => {
    try {
      console.log("Chamando handleLogout...");
  
      if (userProfile && userProfile.id) {
        console.log("Chamando setUserOffline...");
        await setUserOffline(userProfile); // Aguarda o update antes de continuar
      } else {
        console.error("userProfile ou userProfile.id no definido");
      }
  
      // Agora, faa o logout
      if (onLogout) {
        console.log("Chamando onLogout...");
        onLogout(); // Certifique-se de que a funo de logout seja chamada aps o update
      }
  
      // Redirecionar ou outras aes aps o logout
      console.log("Logout completo.");
    } catch (error) {
      console.error("Erro no processo de logout:", error.message);
    }
  };
  














  useEffect(() => {
    window.addEventListener('beforeunload', handleLogout);
    return () => {
      window.removeEventListener('beforeunload', handleLogout);
    };
  }, [userProfile]);

  return (
    <div
      className="login-container"
      style={{
        backgroundImage: `url(${background})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }}
    >
      <div className="login-form-wrapper">
        <Card style={{ maxWidth: 400, margin: 'auto', padding: 20 }}>
          <Title level={3} style={{ textAlign: 'center' }}>
            Login
          </Title>
          <Form layout="vertical" onFinish={handleLogin}>
            <Form.Item
              label="Usurio"
              name="username"
              rules={[{ required: true, message: 'Por favor, insira seu usurio!' }]}
            >
              <Input placeholder="Digite seu usurio" />
            </Form.Item>
            <Form.Item
              label="Senha"
              name="password"
              rules={[{ required: true, message: 'Por favor, insira sua senha!' }]}
            >
              <Input.Password placeholder="Digite sua senha" />
            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" block loading={loading}>
                Entrar
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </div>
    </div>
  );
};

export default Login;
