import React, { useEffect, useState, useCallback } from 'react';
import { List, Avatar, Badge, Dropdown, Card, Button } from 'antd';
import { BellOutlined, DeleteOutlined } from '@ant-design/icons';
import axios from 'axios';

const Notifications = ({ userProfile }) => {
  const [notifications, setNotifications] = useState([]);

  console.log("Inicializando Notifications component");

  const fetchNotifications = useCallback(async () => {
    if (!userProfile || !userProfile.id) {
      console.log("Usurio no est logado ou ID no encontrado.");
      return;
    }

    try {
      console.log("Buscando notificaes para o usurio ID:", userProfile.id);
      
      const response = await axios.get('https://nocodb.nexusnerds.com.br/api/v2/tables/myd2oats63ype1t/records', {
        headers: {
          'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
        },
      });

      console.log("Resposta recebida do servidor:", response.data);

      const allNotifications = response.data.list;

      console.log("Notificaes recebidas:", allNotifications);

      const filteredNotifications = allNotifications.filter(notification => String(notification.userId) === String(userProfile.id));

      console.log("Notificaes filtradas para o usurio logado:", filteredNotifications);

      setNotifications(filteredNotifications);
    } catch (error) {
      console.error('Erro ao buscar notificaes:', error);
    }
  }, [userProfile]);

  useEffect(() => {
    if (userProfile && userProfile.id) {
      console.log("Iniciando o fetch das notificaes para o ID:", userProfile.id);
      fetchNotifications();
      const intervalId = setInterval(fetchNotifications, 30000); // Atualiza a cada 30 segundos
      return () => clearInterval(intervalId);
    }
  }, [fetchNotifications, userProfile]);

  const handleMarkAsRead = async (notification) => {
    try {
      console.log("Marcando como lida:", notification);
  
      const updatedNotification = {
        Id: notification.Id,
        title: notification.title,
        userId: notification.userId,
        message: notification.message,
        isRead: true, // Marcando como lida
        createdAt_: notification.createdAt_ || notification.CreatedAt || new Date().toISOString(), // Mantendo a data original ou usando a atual se estiver null
      };
  
      await axios.patch(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/myd2oats63ype1t/records/`,
        updatedNotification,
        {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
          },
        }
      );
  
      console.log("Notificao marcada como lida com sucesso.");
      fetchNotifications(); // Atualiza as notificaes aps marcar como lida
    } catch (error) {
      console.error('Erro ao marcar notificao como lida:', error);
    }
  };

  const handleDeleteNotification = async (notification) => {
    try {
      console.log("Deletando notificao:", notification);
  
      // Usando a requisio DELETE com o ID no corpo da requisio
      await axios.delete(
        `https://nocodb.nexusnerds.com.br/api/v2/tables/myd2oats63ype1t/records`,
        {
          headers: {
            'xc-token': 'ZqFzoCRvPCyzSRAIKPMbnOaLwR6laivSdxcpXiA5',
            'Content-Type': 'application/json',
          },
          data: { Id: notification.Id }, // Passando o ID no corpo da requisio
        }
      );
  
      console.log("Notificao deletada com sucesso.");
      fetchNotifications(); // Atualiza as notificaes aps a excluso
    } catch (error) {
      console.error('Erro ao deletar a notificao:', error);
    }
  };
  
  

  const menu = (
    <Card className="notification-dropdown" style={{ width: 300 }}>
      <List
        itemLayout="horizontal"
        dataSource={notifications}
        renderItem={(notification) => (
          <List.Item
            onClick={() => handleMarkAsRead(notification)}
            style={{
              borderBottom: '1px solid #f0f0f0',
              padding: '10px 20px',
              cursor: 'pointer',
              transition: 'background 0.3s',
              position: 'relative',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = '#f5f5f5';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = '#fff';
            }}
          >
            <List.Item.Meta
              avatar={<Avatar icon={<BellOutlined />} style={{ backgroundColor: '#87d068' }} />}
              title={
                <span style={{ fontWeight: 'bold', color: '#333' }}>{notification.title || 'Notificao'}</span>
              }
              description={<span style={{ color: '#999' }}>{notification.message}</span>}
            />
            <Button
              type="text"
              icon={<DeleteOutlined />}
              style={{ position: 'absolute', right: 0, top: '50%', transform: 'translateY(-50%)' }}
              onClick={(e) => {
                e.stopPropagation(); // Evitar que o clique marque como lida
                handleDeleteNotification(notification);
              }}
            />
          </List.Item>
        )}
      />
    </Card>
  );

  return (
    <Dropdown overlay={menu} trigger={['click']} placement="bottomRight">
      <Badge
        count={notifications.filter((n) => !n.isRead).length}
        style={{
          color: '#fff',
          fontSize: '12px',
          minWidth: '16px',
          height: '16px',
          lineHeight: '16px',
          padding: '0 4px',
          borderRadius: '50%',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          backgroundColor: '#f5222d',
        }}
        offset={[10, 0]} // Ajusta o posicionamento do badge em relao ao cone
      >
        <BellOutlined style={{ fontSize: '20px', cursor: 'pointer', color: '#fff' }} />
      </Badge>
    </Dropdown>
  );
};

export default Notifications;
