import React from 'react';
import { Avatar, Dropdown, Menu } from 'antd';
import { UserOutlined, LogoutOutlined, EditOutlined, UserAddOutlined, SettingOutlined, AppstoreOutlined, NotificationOutlined, TeamOutlined } from '@ant-design/icons'; // Inclu os novos cones
import { useNavigate } from 'react-router-dom';
import Notifications from './Notifications';

import './Header.css';

const Header = ({ userProfile, onLogout }) => {
  const navigate = useNavigate();

  if (!userProfile) return null;

  if (!userProfile.Cargo1) {
    console.error("Erro: Cargo1 no est definido no userProfile.");
  }

  const handleMenuClick = ({ key }) => {
    if (key === 'config' && userProfile.Cargo1 === 'Desenvolvedor') {
      navigate('/home/config');
    } else if (key === 'editProfile') {
      navigate('/home/editar-perfil');
    } else if (key === 'createUser') {
      navigate('/home/criar-usuario');
    } else if (key === 'manageShortcuts') {
      navigate('/home/gerenciar-atalhos');
    } else if (key === 'sendNotification') {
      navigate('/home/enviar-notificacao');
    } else if (key === 'onlineUsers') {
      navigate('/home/online-users');
    } else if (key === 'logout') {
      onLogout();
    }
  };

  const menu = (
    <Menu onClick={handleMenuClick}>
      <Menu.Item key="editProfile" icon={<EditOutlined />}>
        Editar Perfil
      </Menu.Item>
      {(userProfile.Cargo1 === 'Administrador' || userProfile.Cargo1 === 'Desenvolvedor') && (
        <Menu.Item key="createUser" icon={<UserAddOutlined />}>
          Criar Usurio
        </Menu.Item>
      )}
      <Menu.Item key="manageShortcuts" icon={<AppstoreOutlined />}>
        Atalhos Pessoais
      </Menu.Item>
      {(userProfile.Cargo1 === 'Desenvolvedor' || userProfile.Cargo1 === 'Administrador') && (
        <Menu.Item key="sendNotification" icon={<NotificationOutlined />}>
          Enviar Notificao
        </Menu.Item>
      )}
      {(userProfile.Cargo1 === 'Desenvolvedor' || userProfile.Cargo1 === 'Administrador') && (
        <Menu.Item key="onlineUsers" icon={<TeamOutlined />}>
          Usurios Online
        </Menu.Item>
      )}
      {userProfile.Cargo1 === 'Desenvolvedor' && (
        <Menu.Item key="config" icon={<SettingOutlined />}>
          Configuraes
        </Menu.Item>
      )}
      <Menu.Item key="logout" icon={<LogoutOutlined />}>
        Sair
      </Menu.Item>
    </Menu>
  );

  return (
    <div className="header">
      <div className="header-left">
        <div className="user-info">
          <span>{userProfile.name}</span>
        </div>
      </div>
      <div className="header-right">
        <Notifications userProfile={userProfile} />
        <Dropdown overlay={menu} placement="bottomRight" trigger={['click']}>
          <Avatar
            size={48}
            src={userProfile.profilePic || null}
            icon={!userProfile.profilePic && <UserOutlined />}
            style={{ cursor: 'pointer' }}
          />
        </Dropdown>
      </div>
    </div>
  );
};

export default Header;
