import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Upload, message } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import { uploadImageToFirebase, saveBackgroundConfig } from './firebaseUtils';
import { getDocs, collection, updateDoc } from 'firebase/firestore';
import { db } from './firebaseConfig';
import './ConfigScreen.css';

const ConfigScreen = ({ userProfile }) => {
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (values) => {
    setLoading(true);
    try {
      let backgroundUrl = values.backgroundUrl;

      // Se uma nova imagem foi carregada
      if (values.backgroundImage?.file?.originFileObj) {
        backgroundUrl = await uploadImageToFirebase(values.backgroundImage.file.originFileObj);
      }

      // Salva a configurao no Firestore ou em outro banco de dados
      await saveBackgroundConfig(backgroundUrl);
      message.success('Configurao de fundo salva com sucesso!');
    } catch (error) {
      message.error('Erro ao salvar a configurao: ' + error.message);
    } finally {
      setLoading(false);
    }
  };

  // Funo para adicionar o campo "online" a todos os usurios
  const addOnlineFieldToAllUsers = async () => {
    try {
      const usersSnapshot = await getDocs(collection(db, 'users'));
      usersSnapshot.forEach(async (userDoc) => {
        await updateDoc(userDoc.ref, { online: false });
      });
      console.log('Campo "online" adicionado para todos os usurios.');
    } catch (error) {
      console.error('Erro ao adicionar o campo "online":', error);
    }
  };

  useEffect(() => {
    addOnlineFieldToAllUsers(); // Executa a funo ao carregar a pgina
  }, []);

  // Exibe a tela de configurao apenas para Desenvolvedores
  if (userProfile.Cargo1 !== 'Desenvolvedor') {
    return <p>Voc no tem permisso para acessar esta pgina.</p>;
  }

  return (
    <div className="config-screen">
      <h2>Configurar Fundo da Tela de Login</h2>
      <Form onFinish={handleSubmit} layout="vertical">
        <Form.Item
          label="Link da Imagem de Fundo"
          name="backgroundUrl"
        >
          <Input placeholder="Digite o link da imagem" />
        </Form.Item>
        <Form.Item
          label="Upload de Imagem de Fundo"
          name="backgroundImage"
        >
          <Upload listType="picture" maxCount={1}>
            <Button icon={<UploadOutlined />}>Carregar Imagem</Button>
          </Upload>
        </Form.Item>
        <Button type="primary" htmlType="submit" loading={loading}>
          Salvar Configurao
        </Button>
      </Form>
    </div>
  );
};

export default ConfigScreen;
